import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index/Index'
import Camp from '@/components/activity/Camp'
import Details from '@/components/activity/Details'
import Parentchild from '@/components/activity/Parentchild'
import Solidarity from '@/components/activity/Solidarity'
import Company from '@/components/baseball/Company'
import Team from '@/components/baseball/Team'
import Contact from '@/components/contact/Contact'
import Adult from '@/components/training/Adult'
import Child from '@/components/training/Child'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'Index',
      component: Index,
      meta: {
        title: '绅力棒球 | 首页' // 标题设置
      }
    },
    {
      path: '/camp',
      name: 'Camp',
      component: Camp,
      meta: {
        title: '绅力棒球 | 冬夏令营' // 标题设置
      }
    },
    {
      path: '/details',
      name: 'Details',
      component: Details,
      meta: {
        title: '绅力棒球 | 详情' // 标题设置
      }
    },
    {
      path: '/parentchild',
      name: 'Parentchild',
      component: Parentchild,
      meta: {
        title: '绅力棒球 | 亲子活动' // 标题设置
      }
    },
    {
      path: '/solidarity',
      name: 'Solidarity',
      component: Solidarity,
      meta: {
        title: '绅力棒球 | 团建拓展' // 标题设置
      }
    },
    {
      path: '/company',
      name: 'Company',
      component: Company,
      meta: {
        title: '绅力棒球 | 公司简介' // 标题设置
      }
    },
    {
      path: '/team',
      name: 'Team',
      component: Team,
      meta: {
        title: '绅力棒球 | 教练团队' // 标题设置
      }
    },
    {
      path: '/contact',
      name: 'Contact',
      component: Contact,
      meta: {
        title: '绅力棒球 | 联系方式' // 标题设置
      }

    },
    {
      path: '/adult',
      name: 'Adult',
      component: Adult,
      meta: {
        title: '绅力棒球 | 成人培训' // 标题设置
      }
    },
    {
      path: '/child',
      name: 'Child',
      component: Child,
      meta: {
        title: '绅力棒球 | 青年培训' // 标题设置
      }
    }
  ]
})



