<template>
  <div id="Header">
    <div class="navbar-wrapper">
      <div class="container">
        <div class="navbar navbar-inverse">
          <div class="navbar-inner">
            <a class="brand" @click="routerindex()">
              <img :src="logoimg" alt style="width:80px;height:80px">
            </a>
            <div class="nav-collapse collapse">
              <ul id="navBar" class="nav main">
                <li
                  class="dropdown"
                  :class="{active:navchangeIndex == index}"
                  @click="navchange(index)"
                  v-for="(item,index) in navbarlist"
                  :key="index"
                >
                  <a class="dropdown-toggle" data-toggle="dropdown">{{item.title1}}</a>
                  <ul class="dropdown-menu navbar-nav menu6">
                    <li
                      v-for="(item,index) in item.title2"
                      :key="index"
                      @click="childnavchange(item.num)"
                    >
                      <a>{{item.name}}</a>
                    </li>
                  </ul>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div
          v-for="(item,index) in slidelist"
          :key="index"
          class="swiper-slide"
          style="height:550px;margin-top:90px;"
        >
          <img :src="item.image" alt style="width:100%;height:100%;">
        </div>
      </div>
      <!-- Add Arrows -->
      <div class="swiper-button-next left carousel-control"></div>
      <div class="swiper-button-prev"></div>
    </div>
  </div>
</template>

<script>
$(function() {
  //滚动函数
  function d() {
    if (c == e.length) return !1;
    var f = $(e[c]).offset().top,
      b = $(window).scrollTop() + g - 250;

    if (navigator.userAgent.match(/(iPhone|iPod|Android|ios)/i)) {
      b = $(window).scrollTop() + g - 200;
    }

    if (f < b)
      if (($(e[c]).removeClass("unScrollTo"), c++, c < e.length)) d();
      else return !1;
    else return !1;
  }
  var e = $(".unScrollTo"),
    c = 0,
    g = window.screen.availHeight;

  $(window).scroll(function() {
    d();
  });
  d();

  //动态边框
  var $animateBorder = $(".animateBorder");
  var str =
    '<div class="bdLeft"></div><div class="bdBottom"></div><div class="bdTop"></div><div class="bdRight"></div>';
  //插入模拟边框的div
  $animateBorder.append(str);
  //动态
  $animateBorder.hover(
    function() {
      $(this)
        .find(".bdLeft,.bdRight")
        .stop()
        .animate(
          {
            height: "100%"
          },
          500
        );
      $(this)
        .find(".bdTop,.bdBottom")
        .stop()
        .animate(
          {
            width: "100%"
          },
          500
        );
    },
    function() {
      $(this)
        .find(".bdLeft,.bdRight")
        .stop()
        .animate(
          {
            height: "0"
          },
          500
        );
      $(this)
        .find(".bdTop,.bdBottom")
        .stop()
        .animate(
          {
            width: "0"
          },
          500
        );
    }
  );
});

import Swiper from "swiper";
export default {
  name: "Header",
  data() {
    return {
      navchangeIndex: 0,
      logoimg: "./static/lib/image/01.png",
      navbarlist: [
        { title1: "首页" },
        {
          title1: "棒球培训",
          title2: [
            { num: "11", name: "青少年培训" },
            { num: "12", name: "成人培训" }
          ]
        },
        {
          title1: "棒球活动",

          title2: [
            { num: "21", name: "团建拓展" },
            { num: "22", name: "亲子活动" },
            { num: "23", name: "冬夏令营" }
          ]
        },
        {
          title1: "绅力棒球",
          title2: [
            { num: "31", name: "公司简介" },
            { num: "32", name: "教练团队" }
          ]
        },
        { title1: "联系方式" }
      ],
      slidelist: [
        { image: "./static/lib/image/banner1.png" },
        { image: "./static/lib/image/banner2.png" },
        { image: "./static/lib/image/banner3.png" },
        { image: "./static/lib/image/banner4.png" }
      ]
    };
  },
  methods: {
    getnavnum() {
      this.navchangeIndex = sessionStorage.getItem("navnum");
    },
    setnavnum(index) {
      sessionStorage.setItem("navnum", index);
    },
    navchange(index) {
      this.setnavnum(index);
      if (index == 0) {
        this.$router.push("/");
        location.reload();
      } else if (index == 4) {
        this.$router.push("/contact");
        location.reload();
      }
    },
    childnavchange(index) {
      if (index == 11) {
        this.setnavnum(1);
        this.$router.push("/child");
      } else if (index == 12) {
        this.setnavnum(1);
        this.$router.push("/adult");
      } else if (index == 21) {
        this.setnavnum(2);
        this.$router.push("/solidarity");
      } else if (index == 22) {
        this.setnavnum(2);
        this.$router.push("/parentchild");
      } else if (index == 23) {
        this.setnavnum(2);
        this.$router.push("/camp");
      } else if (index == 31) {
        this.setnavnum(3);
        this.$router.push("/company");
      } else if (index == 32) {
        this.setnavnum(3);
        this.$router.push("/team");
      }
      location.reload();
    },
    routerindex() {
      this.$router.push("/");
      sessionStorage.setItem("navnum", 0);
      location.reload();
    }
  },
  mounted() {
    this.getnavnum();
    var swiper = new Swiper(".swiper-container", {
      loop: true,
      autoplay: true,
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev"
      }
    });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a {
  cursor: pointer;
}
.swiper-container {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  height: 400px;
  margin: 40px auto;
  text-align: center;
  font-size: 18px;
  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-pagination-bullet {
  width: 20px;
  height: 20px;
  text-align: center;
  line-height: 20px;
  font-size: 12px;
  color: #000;
  opacity: 1;
  background: rgba(0, 0, 0, 0.2);
}

.swiper-pagination-bullet-active {
  color: #fff;
  background: #ffb72b;
}
</style>


