<template>
  <div id="parentchild">
    <Header></Header>
    <div class="container training-module1">
      <div class="public-title tac mg0">
        <h4>{{Parentchild_module_Ctitle}}</h4>
        <p>{{Parentchild_module_Etitle}}</p>
        <div class="line tac"></div>
      </div>
    </div>
    <div class="container solidarity-module2 transition unScrollTo">
      <div
        class="solidarity-module2-img baseimglight hoverflow"
        v-for="(item,index) in parentchild_module2_list"
        :key="index"
      >
        <div class="solidarity-module2-img-tm"></div>
        <img :src="item.img" :alt="index" style="width:100%;height:100%;">
        <div class="solidarity-module2-img-value tac">
          <h5>{{item.title}}</h5>
          <p>
            <a @click="turnRouter(item.id)">了解详情</a>
          </p>
        </div>
      </div>
      <div id="demo2"></div>
      <div class="more baseimglight hoverflow">查看更多</div>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
import Header from "../public/Header";
import Footer from "../public/Footer";
export default {
  name: "Parentchild",

  components: {
    Header,
    Footer
  },
  data() {
    return {
      Parentchild_module_Ctitle: "亲子活动",
      Parentchild_module_Etitle: "PARTENT CHILD ACTIVITY",
      parentchild_module2_list: [
        {
          id: 1,
          title: "星乐LIVE团建活动",
          img: "./static/lib/image/active1.png"
        },
        {
          id: 2,
          title: "星乐LIVE团建活动",
          img: "./static/lib/image/active1.png"
        },
        {
          id: 3,
          title: "星乐LIVE团建活动",
          img: "./static/lib/image/active1.png"
        }
      ]
    };
  },

  methods: {
    turnRouter(id) {
      this.$router.push({
        path: "/details", //要跳转的页面路径
        query: {
          id: id //传递的参数，方法要接收ins
        }
      });
      sessionStorage.setItem("navnum", 2);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>


