<template>
  <div class="foot">
    <div class="container" style="position: relative;">
      <div class="footer">
        <h5>{{conpanymessage.titel}}</h5>
        <p class="footer-addres">{{conpanymessage.address}}</p>
        <p>{{conpanymessage.phone}}</p>
        <p class="footer-last">{{conpanymessage.email}}</p>
      </div>
      <div class="footer" v-for="(item,index) in messagelist" :key="index">
        <h5 class="tac">{{item.title}}</h5>
        <div class="footer-erimg baseimglight hoverflow">
          <img :src="item.images" alt="二维码" width="100%">
        </div>
      </div>
      <div class="footer footer-l">
        <h5>{{footer3.title1}}</h5>
        <h6 v-for="(item,index) in footer3.child" :key="index">
          <a @click="routerChange(item.url)">{{item.title2}}</a>
        </h6>
      </div>
      <div class="footer-information">
        <p>{{copy}}</p>
      </div>
    </div>
    <div class="footer-logo">
      <img :src="footerlogo" alt="logo" width="100%">
    </div>
  </div>
</template>

<script>
export default {
  name: "Footer",
  data() {
    return {
      conpanymessage: {
        title: "俱乐部焦点",
        address: "地址：深圳市福田区车公庙皇冠体育中心羽毛球馆头舱2楼",
        phone: "电话：188 2341 1343(段老师)",
        email: "邮箱：1418506615@qq.com"
      },
      messagelist: [
        { title: "绅力公众号", images: "./static/lib/image/footer-er.png" },
        { title: "绅力微信", images: "./static/lib/image/footer-er.png" }
      ],
      footer3: {
        title1: "绅力活动",
        child: [
          { url: "/solidarity", title2: "团建拓展" },
          { url: "/parentchild", title2: "亲子活动" },
          { url: "/camp", title2: "冬夏令营" }
        ]
      },
      copy:
        "Copyright © 2016.Company name All rights reserved.More Templates Collect from",
      footerlogo: "./static/lib/image/footer-logo.png"
    };
  },
  methods: {
    routerChange(url) {
      this.$router.push(url);
      sessionStorage.setItem("navnum", 2);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>


