<template>
  <div id="contact">
    <Header></Header>
    <div class="container training-module1 transition unScrollTo">
      <div class="public-title tac mg0">
        <h4>联系方式</h4>
        <p>SHENGLI CULD</p>
        <div class="line tac"></div>
      </div>
    </div>
    <div class="container">
      <div class="form_right f-left">
        <div class="training-module1 contant">
          <div class="public-title mg0">
            <h4>Hi，are you ready?</h4>
            <div class="contant-line"></div>
            <p>准备好开始了吗？</p>
            <p>那就与我们取得联系吧</p>
          </div>
        </div>
        <div class="contant-module1">
          <div class="contant-module1-value">
            <h5>{{companymeg.title}}</h5>
            <p>{{companymeg.address}}</p>
            <p>{{companymeg.phone}}</p>
            <p>{{companymeg.email}}</p>
            <p>{{companymeg.watset}}</p>
          </div>
        </div>
      </div>
      <div class="adult_module4_form_title">
        <h5>
          即刻预约&nbsp;&nbsp;
          <span>绅力棒球</span>&nbsp;&nbsp;欢迎体验
        </h5>
      </div>
      <div class="adult_module4_form animateBorder1">
        <form class="adult_module4_form_form" method="post" novalidate="novalidate">
          <div>
            <span>
              <input
                type="text"
                name="name"
                id="name"
                value
                size="40"
                aria-invalid="false"
                placeholder="您的称呼(必填)"
              >
            </span>
          </div>
          <div>
            <span>
              <input
                type="text"
                name="phone"
                id="phone"
                value
                size="40"
                aria-invalid="false"
                placeholder="您的电话(必填)"
              >
            </span>
          </div>
          <div>
            <span>
              <input
                type="text"
                name="son"
                id="son"
                value
                size="40"
                aria-invalid="false"
                placeholder="孩子名字(必填)"
              >
            </span>
          </div>
          <div>
            <span>
              <input
                type="text"
                name="age"
                id="age"
                value
                size="40"
                aria-invalid="false"
                placeholder="孩子年龄(必填)"
              >
            </span>
          </div>
          <div class="delectbox">
            <select id="selectschool" class="selectschool" name="gloryid">
              <option value="0">请选择校区</option>
              <option value="1">金洲小学</option>
              <option value="7">大塘小学</option>
              <option value="6">麒麟小学</option>
              <option value="8">祈福英语实验小学</option>
              <option value="9">南沙第一中学</option>
              <option value="10">朝阳中学</option>
              <option value="11">番禺兴趣班</option>
              <option value="14">绅力训练基地</option>
            </select>
            <select id="demo4" class="selectteam" name="campusid">
              <option value="0">请选择队伍</option>
            </select>
          </div>
          <div>
            <p id="btns" class="f_f_submit">提交</p>
          </div>
        </form>
      </div>
    </div>
    <div class="container contant-module2 transition unScrollTo">
      <div class="contant-module2-value" v-for="(item,index) in contactlist" :key="index">
        <img :src="item.image" alt="图片">
        <p>{{item.title}}</p>
      </div>
    </div>
    <!-- 地图 -->
    <div class="container transition unScrollTo">
      <div class="row">
        <div
          class="col-md-12"
          id="baiduMap"
          style="height:600px;filter: drop-shadow(0 0 2px rgba(0, 0, 0, 0.4));"
        ></div>
      </div>
    </div>
    <div class="container training-module1 contant mb100 transition unScrollTo">
      <div class="public-title tac mg0">
        <h4>Are you interest in?</h4>
        <div class="contant-line mg0a"></div>
        <p>你有兴趣吗？</p>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
import Header from "../public/Header";
import Footer from "../public/Footer";
export default {
  name: "Contact",
  components: {
    Header,
    Footer
  },
  data() {
    return {
      companymeg: {
        title: "绅力棒球俱乐部",
        address: "地址：广州市南沙区绅力棒球上湾训练基地",
        phone: "电话：13590142476（林教练）",
        email: "邮箱：1317181315@qq.com",
        watset: "微信：13590142476"
      },
      contactlist: [
        {
          image: "./static/lib/image/contant1.png",
          title: "关注微信一举一动尽在掌握"
        },
        {
          image: "./static/lib/image/contant2.png",
          title: "联系电话：188 2341 1343"
        },
        {
          image: "./static/lib/image/contant3.png",
          title: "在线QQ:123123123123"
        }
      ]
    };
  },

  methods: {}
};

$(function() {
  var map = new BMap.Map("baiduMap");
  var point = new BMap.Point(113.5927257874, 22.8067698849); //地址的坐标
  map.centerAndZoom(point, 16);
  map.enableScrollWheelZoom(); //开启滚轮缩放
  var marker = new BMap.Marker(point); // 创建标注
  map.addOverlay(marker); // 将标注添加到地图中
  var opts = {
    width: 200, // 信息窗口宽度
    height: 100, // 信息窗口高度
    title: "广州绅力体育有限公司", // 信息窗口标题
    enableMessage: true, //设置允许信息窗发送短息
    message: "地址：广州市南沙区绅力棒球上湾训练基地"
  };
  var infoWindow = new BMap.InfoWindow(
    "地址：广州市南沙区绅力棒球上湾训练基地",
    opts
  ); // 创建信息窗口对象
  map.openInfoWindow(infoWindow, point); //开启信息窗口
  marker.addEventListener("click", function() {
    map.openInfoWindow(infoWindow, point); //开启信息窗口
  });
});
$(function() {
  $("#selectschool").bind("change", function() {
    var id = $("#selectschool")
      .find("option:selected")
      .val();
    $.post(
      "/Home/Contact/selectteam",
      { id: id },
      function(data) {
        if (data.length != undefined) {
          var teml = "<div>";
          for (var i = 0; i < data.length; i++) {
            teml +=
              "<option  value=" +
              data[i].id +
              ">" +
              data[i].title +
              "</option>";
          }
          teml += "</div>";
          $("#demo4").html(teml);
        } else {
          $("#demo4").html('<option value="0">没有队伍</option>');
        }
      },
      "json"
    );
  });
});
$("#btns").click(function() {
  var name = $("#name").val();
  var phone = $("#phone").val();
  var son = $("#son").val();
  var age = $("#age").val();
  var gloryid = $("#selectschool").val();
  var campusid = $("#demo4").val();
  if (!/^[\u4e00-\u9fa5a-zA-Z]+$/.test(name)) {
    alert("姓名只能为中文或英文！！！");
    exit();
  }
  if (
    !/^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/.test(
      phone
    )
  ) {
    alert("请输入正确的手机号！！！");
    exit();
  }
  if (!/^[\u4e00-\u9fa5a-zA-Z]+$/.test(son)) {
    alert("孩子姓名只能为中文或英文！！！");
    exit();
  }
  if (!/^[0-9]{1,}$/.test(age)) {
    alert("年龄只能为整数！！！");
    exit();
  }
  if (gloryid == 0) {
    alert("请选择校区！！");
    exit();
  }
  $.ajax({
    url: "/index.php/Home/Contact/sign",
    data: {
      name: name,
      phone: phone,
      son: son,
      age: age,
      gloryid: gloryid,
      campusid: campusid
    },
    dataType: "TEXT",
    type: "POST",
    success: function(data) {
      alert(data);
    }
  });
});
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>


